$(function () {
    $('.single-item').slick({
        infinite: true,
        slidesToShow: 1,
        slidesToScroll: 1,
        arrows: true,
        nextArrow: '<img src="./img/left.svg" alt="left arrow" style="position: absolute; top: 50%; left: 0; cursor: pointer;";>',
        prevArrow: '<img src="./img/left.svg" alt="right arrow" style="transform: rotate(180deg); position: absolute; right: 0; z-index: 2; top: 50%; cursor: pointer;";>'
    })
});